body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #a5a2a2;
  height: 100vh;
}
body.fijar-body {
  overflow: hidden;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

main {
  background-image: linear-gradient(to top, #352ed3, #382ed0, #3b2ecc, #3d2ec9, #3f2ec6, #412ec7, #422ec9, #442eca, #462ed0, #482ed5, #4b2edb, #4d2ee1);
  background-repeat: no-repeat;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Open Sans", sans-serif;
}

h2 {
  font-size: 4rem;
  font-family: "Roboto", cursive;
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #FFFFFF;
  font-size: 1.6rem;
  line-height: 1.8;
  font-family: "Open Sans", sans-serif;
}

.header__barra {
  padding: 2rem 5%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 1000;
  font-size: 2rem;
}
.header__barra .navegacion {
  display: none;
}
@media (min-width: 1024px) {
  .header__barra .navegacion {
    display: block;
  }
}
.header__barra .navegacion ul {
  display: flex;
  align-items: center;
}
.header__barra .navegacion ul li {
  margin-left: 1rem;
  display: block;
}
.header__barra .navegacion ul li a {
  color: #FFFFFF;
  position: relative;
}
.header__barra .navegacion ul li a::before {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  display: block;
  transition: all 0.5s ease-in-out;
}
.header__barra .navegacion ul li a:hover {
  cursor: pointer;
  color: #FFFFFF;
  opacity: 0.7;
}
.header__barra .navegacion ul li a:hover::before {
  transform: scaleX(1);
  background-color: #412EC2;
}
.header__barra .navegacion ul li:last-child a {
  font-size: 2.1rem;
  font-weight: 600;
}
.header__barra .navegacion ul li:last-child a:hover {
  color: #412EC2;
}
.header__barra .navegacion ul li:last-child a::before {
  transform: scaleX(0);
}
.header__barra .navegacion ul li .boton {
  margin-top: 0;
}

.logo {
  font-size: 3rem;
  font-weight: 700;
  color: #FFFFFF;
  z-index: 5;
}

.toggle {
  position: relative;
  width: 50px;
  height: 30px;
  background: transparent;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.08);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  z-index: 5;
}
@media (min-width: 1024px) {
  .toggle {
    display: none;
  }
}
.toggle span {
  position: absolute;
  width: 20px;
  height: 2px;
  background: #FFFFFF;
  border-radius: 4px;
  transition: 0.5s;
}
.toggle span:nth-child(1) {
  transform: translateY(-8px);
  width: 20px;
  left: 15px;
}
.toggle span:nth-child(2) {
  transform: translateY(8px);
  width: 20px;
  left: 15px;
}
.toggle.active {
  background: #412EC2;
}
.toggle.active span:nth-child(1) {
  width: 20px;
  transform: translateY(0px) rotate(45deg);
  transition-delay: 0.125s;
}
.toggle.active span:nth-child(2) {
  width: 20px;
  transform: translateY(0px) rotate(315deg);
  transition-delay: 0.25s;
}
.toggle.active span:nth-child(3) {
  transform: translateX(60px);
}

h1 {
  font-size: 63px;
  font-weight: 400;
  color: #FFFFFF;
}

.header-top {
  height: 100%;
  width: 100%;
  background-image: url(../img/programacion.jpg);
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  z-index: 999;
  background-repeat: nore;
  position: relative;
}

.text-start-center {
  text-align: lef;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

b i {
  color: #412EC2;
  font-style: normal;
}

.social-media {
  font-size: 25px;
  text-align: left;
  margin: 0;
  padding: 15px 0;
}

.social-media a {
  color: #FFFFFF;
  text-decoration: none;
  outline: 0 none;
  padding: 0 10px;
}

.social-media a:hover {
  color: #412EC2;
}

.social-media .tooltip.bottom .tooltip-arrow {
  border-bottom-color: #412EC2;
}

.social-media .tooltip-inner {
  background-color: transparent;
  color: #412EC2;
  font-size: 16px;
}

.social__icon {
  position: relative;
}
.social__icon .social__content-icon {
  background-color: #412EC2;
  color: #FFFFFF;
  border-radius: 1.4rem;
  position: absolute;
  width: 60px;
  transform: translateY(30px) translateX(-35%);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.4s ease-in-out;
  opacity: 0;
  visibility: hidden;
  font-size: 1.3rem;
  padding: 5px 8px;
  font-weight: 700;
  font-family: "Roboto", cursive;
}
.social__icon .social__content-icon::before {
  background-color: #412EC2;
  content: "";
  height: 10px;
  position: absolute;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  width: 10px;
  top: -3px;
  z-index: -12;
}
.social__icon:hover .social__content-icon {
  opacity: 1;
  visibility: visible;
  transform: translateY(7px) translateX(-35%);
}

#particles-js {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  background: rgba(0, 0, 0, 0.3);
}

.boton {
  position: relative;
  background-color: rgba(255, 255, 255, 0.315);
  padding: 1rem 3rem;
  color: #FFFFFF;
  box-shadow: 1px 1px 30px -5px #5880f6;
  margin-top: 2rem;
  font-size: 1.8rem;
  border-radius: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition: all 0.15s ease-in-out;
  text-align: center;
  text-decoration: none;
  letter-spacing: 1px;
  overflow: hidden;
}
.boton span {
  position: relative;
  z-index: 2;
}
.boton::before {
  content: "";
  position: absolute;
  top: var(--y);
  left: var(--x);
  transform: translate(-50%, -50%);
  width: 0px;
  height: 0px;
  border-radius: 50px;
  background: #412EC2;
  z-index: 0;
  transition: all 0.7s;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  transition: width 0.15s, height 0.15s;
  box-shadow: 1px 1px 30px -5px #449de6;
}
.boton:hover::before {
  transition: all 0.7s;
  width: 400px;
  height: 300px;
  border-radius: 50%;
}

#nav {
  position: fixed;
  z-index: 20;
  right: 20px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

#nav a {
  font-size: 0px;
  line-height: 0px;
  display: block;
  background: transparent;
  margin: 10px 0;
  -webkit-filter: drop-shadow(0 0 4px rgba(0, 0, 0, 0.5));
  filter: drop-shadow(0 0 4px rgba(0, 0, 0, 0.5));
}

#nav a:not(.top) {
  width: 20px;
  height: 20px;
  border: 4px solid #FFF;
  border-radius: 50%;
}

section {
  width: 100%;
  min-height: 100vh;
  display: -webkit-box;
  display: flex;
  flex-direction: column;
  color: #FFF;
  padding: 5rem 5%;
}

section#section-1, [data-scrolltosmooth-expand=top] {
  background-color: #FFFFFF;
  /**
   * Photo by Paul IJsendoorn from Pexels
   */
  background-size: cover;
  background-attachment: fixed;
}

.scroll-icon {
  position: absolute;
  right: 0;
  left: 0;
  margin: auto;
  bottom: 5%;
  z-index: 11;
  width: 38px;
  height: 63px;
  border-radius: 25px;
  box-shadow: inset 0 0 0 1px white;
}

.scroll-icon:before {
  content: "";
  position: absolute;
  left: 50%;
  background-color: white;
  width: 8px;
  height: 8px;
  margin-left: -4px;
  top: 8px;
  border-radius: 4px !important;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-name: scroll;
}

@keyframes scroll {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateY(36px);
  }
}
.menuMobile {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #412EC2;
  transform: translateY(-100vh);
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s ease-in-out;
}
.menuMobile.active {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
}
.menuMobile .mobile__contenedor {
  position: relative;
  display: flex;
  height: 100%;
}
.menuMobile .mobile__contenedor .mobile__navegacion {
  width: 100%;
  position: absolute;
  z-index: 1;
  height: 100%;
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .menuMobile .mobile__contenedor .mobile__navegacion {
    width: 50%;
    position: relative;
  }
}
.menuMobile .mobile__contenedor .mobile__navegacion ul li {
  margin: 2rem 0;
}
.menuMobile .mobile__contenedor .mobile__navegacion ul li .mobile__navegacion-item {
  position: relative;
  background-color: #FFFFFF;
  border-radius: 2rem;
  font-weight: bold;
  font-size: 3rem;
  padding: 0rem 1.5rem;
  transition: all 0.3s;
  overflow: hidden;
  transition: all 0.5s;
}
.menuMobile .mobile__contenedor .mobile__navegacion ul li .mobile__navegacion-item.active {
  padding-left: 3.5rem;
}
.menuMobile .mobile__contenedor .mobile__navegacion ul li .mobile__navegacion-item::after {
  background: url(../img/dollar.png);
  content: "";
  background-size: cover;
  display: block;
  height: 26px;
  opacity: 0;
  position: absolute;
  z-index: 7;
  top: 20%;
  transition: all 0.6s ease;
  visibility: hidden;
  width: 26px;
  margin: 0 2px;
}
.menuMobile .mobile__contenedor .mobile__navegacion ul li .mobile__navegacion-item.active::after {
  visibility: visible;
  opacity: 1;
  transition-delay: 0.25s;
}
.menuMobile .mobile__contenedor .mobile__container-image {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #2e1b85 url(../img/25498.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .menuMobile .mobile__contenedor .mobile__container-image {
    position: relative;
    width: 50%;
  }
}

h3.heading {
  position: relative;
  font-family: "Open Sans", sans-serif;
  font-weight: 500;
