.box1 {
  background: #01081f;
  padding: 1.5rem;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  z-index: 1;
  box-shadow: rgba(231, 231, 231, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}
.box1 i {
  padding: 2rem;
  line-height: 40px;
  font-size: 5rem;
  color: white;
}
.box1 .box__content {
  color: #FFFFFF;
  text-align: center;
  padding-top: 25px;
}
.box1 .box__content p {
  font-size: 3rem;
  font-weight: 700;
}
.box1 h5 {
  font-family: "Poppins", sans-serif;
  margin-bottom: 30px;
  color: white;
  font-size: 25px;
}

.box1:before {
  content: "";
  position: absolute;
  top: -220px;
  left: -220px;
  width: 200px;
  height: 200px;
  border-radius: 0;
  z-index: -1;
  background: #01081f;
  transition: cubic-bezier(0.39, 0.58, 0.57, 1) all 0.55s;
}

.box1:after {
  content: "";
  position: absolute;
  top: -72px;
  left: -40px;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  z-index: -2;
  background-color: #f5d020;
  background-image: linear-gradient(315deg, #412EC2 0%, #352ED3 74%);
  transition: cubic-bezier(0.39, 0.58, 0.57, 1) all 0.15s;
}

.box1:hover:before {
  top: -72px;
  left: -40px;
  border-radius: 50%;
}

.box1:hover:after {
  content: "";
  width: 120%;
  left: -10%;
  top: -10%;
  height: 120%;
  border-radius: 0;
  transition: cubic-bezier(0.39, 0.58, 0.57, 1) all 0.55s;
}

#services {
  background-color: #01081f;
}
#services .nav-tabs {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
