.services__container .services__content.fade {
  transition: display 0.15s linear;
}
.services__container .services__content.hide {
  display: none;
  opacity: 0;
  visibility: hidden;
}
.services__container .services-grid {
  text-align: center;
}
@media (min-width: 1024px) {
  .services__container .services-grid {
    display: grid;
    gap: 2rem;
